import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import CatalogItem from './CatalogueItem';
import styles from './Catalogue.module.scss';
import {
	CatalogueQuery,
	CatalogueQuery_catalogues_nodes,
} from './__generated__/CatalogueQuery';
import useIntlHelper from '~components/utils/Intl/IntlHelper';

export const Catalogue: React.FC = () => {
	const data: CatalogueQuery = useStaticQuery(QUERY);

	return (
		<article className="post-full post no-image page no-image">
			<section>
				<div className={styles.flexWrapper}>
					{data.catalogues.nodes.map(
						(catalogue: CatalogueQuery_catalogues_nodes, i: number) => {
							return <CatalogItem catalogue={catalogue} key={i} />;
						},
					)}
				</div>
			</section>
		</article>
	);
};

const QUERY = graphql`
	query CatalogueQuery {
		catalogues: allCatalogueYaml {
			nodes {
				id
				link
				date(formatString: "DD MMMM YYYY")
				image {
					childImageSharp {
						fixed(width: 150) {
							...GatsbyImageSharpFixed_withWebp_tracedSVG
						}
					}
				}
			}
		}
	}
`;
